import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/main'
import Index from '@/components/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/:api/:page',
      name: 'appmain',
      component: HelloWorld
    },
    {
      path: '/',
      name: 'index',
      component: Index
    }
  ]
})



